import React, { Component } from 'react';
import { Container } from 'reactstrap';


class Projects extends Component {
  render() {
    return (
        <div className='page text-center d-flex align-items-center' id='projects'>
          <Container>
            <h1>Projects Page</h1>
          </ Container>
        </div>
      
    );
  }
}

export default Projects;



