import React, { Component } from 'react';
import { Container } from 'reactstrap';


class Contact extends Component {
  render() {
    return (
        <div className='page text-center d-flex align-items-center' id='contact'>
          <Container>
            <h1>Contact Page</h1>
          </ Container>
        </div>
      
    );
  }
}

export default Contact;



